export default {
	data() {
		return {
			hasSendCode: false,
			codeSecond: 60,
			mobile: '',
			code: ''
		}
	},
	methods: {
		clearIcon() {
			let that = this;
			that.mobile = "";
		},
		getCode() {
			let that = this;
			that.hasSendCode = true;
			var interval = setInterval(() => {
				--that.codeSecond
			}, 1000)
			setTimeout(() => {
				clearInterval(interval)
				that.hasSendCode = false;
				that.codeSecond=60;
			}, 60000)
		},
		next(){
			let that = this;
			uni.navigateTo({
				url:'./setPassword'
			})
		}
	}
}
